/* End opacity
-------------------------------*/
.loader {
  background-color: #242323;
  height: 100vh;
  position: relative;
}

.loader.fade {
  display: none;
}

.loader .loader__head {
  width: 150px;
  height: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader .loader__head img {
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
}

.loader .loader__head .dots {
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader .loader__head .dots span {
  height: 20px;
  width: 20px;
  border: 1px solid #b69d74;
  border-radius: 50%;
  margin-right: 5px;
  display: inline-block;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader .loader__head .dots span > span {
  width: 12px;
  height: 12px;
  background-color: #b69d74;
  border-radius: 50%;
  margin: 0;
  animation: preloader 1.3s infinite ease-in-out;
}

.loader .loader__head .dots span > span.dot_1 {
  animation-delay: .3s;
}

.loader .loader__head .dots span > span.dot_2 {
  animation-delay: .4s;
}

.loader .loader__head .dots span > span.dot_3 {
  animation-delay: .5s;
}

.loader .loader__head .dots span > span.dot_4 {
  animation-delay: .6s;
}

.loader .loader__head .dots span > span.dot_5 {
  animation-delay: .7s;
}

@keyframes preloader {
  0%,
  70%,
  100% {
    transform: scale3D(1.3, 1.3, 1.3);
  }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1);
  }
}
